import React, { Component } from 'react';
import { connect } from 'react-redux';

import * as stepActions from '../steppers/state/actions';
import * as actions from '../state/actions';
import StepZilla from '../steps/main'
import '../steps/css/main.css'
import DisputeCreditTransaction from '../components/dispute-workflow/DisputeCreditTransaction';
import DecisionQuestionApp from '../components/dispute-workflow/DecisionQuestionApp';
import CreditCardQuestionnaire from '../components/dispute-workflow/CreditCardQuestionnaire';
import DynamicQuestionnaire from '../components/dispute-workflow/DynamicQuestionnaire';
import ExtraInfo from '../components/dispute-workflow/ExtraInfo';
import DisputeConfirmation from '../components/dispute-workflow/DisputeConfirmation';
import Confirmed from '../components/dispute-workflow/Confirmed';
import ProgressBar from '../components/UI-component/ProgressBar';

import * as constant from '../shared/constant';
class MainForm extends Component {

    componentDidMount = () => {

        const accessToken = localStorage.getItem("accessToken");
        // const accessToken = "39b5333ff62de26bd4c3a91c4d32eb64";

        if (accessToken) {
            this.props.accessToken(accessToken);
            this.props.remoteGetUserProfile(accessToken);
        }


        // const transactionDisputes = constant.ARYA_STARK_GET_TRANSACTION_HISTORY_PAYLOAD;
        // this.props.addDispute(transactionDisputes);

        const transactionDisputes = localStorage.getItem("transactionDisputes");
        if (transactionDisputes) {
            const transactionDisputesjs = JSON.parse(transactionDisputes);
            this.props.addDispute(transactionDisputesjs);
        }
    }

    render() {
        const steps =
            [
                { name: 'Step 1', component: <DisputeCreditTransaction history={this.props.history} /> },
                { name: 'Step 2', component: <DecisionQuestionApp history={this.props.history} /> },
                { name: 'QUESTIONNAIRE', component: <CreditCardQuestionnaire /> },
                // { name: 'Step 4', component: <AdditionalQuestionnaire /> },
                { name: 'Step 4', component: <DynamicQuestionnaire /> },
                { name: 'Step 5', component: <ExtraInfo /> },
                { name: 'CONFIRMATION', component: <DisputeConfirmation /> },
                { name: 'SUBMIT', component: <Confirmed /> }
            ]

        if (this.props.userProfile != null) {
            var userProfile = this.props.userProfile;
            const transactionDisputes = this.props.transactionDisputes;
            const tranx = [];
            var transxSum = 0;
            transactionDisputes.map((item) => {
                tranx.push(item.id);
                transxSum = transxSum + item.Amount;
            })
            userProfile.tranx = tranx;
            userProfile.transxSum = transxSum;
            this.props.setUserProfile(userProfile);
        }

        return (
            <div className="row">
                <div className="col-lg-12">
                    <div className="ibox float-e-margins">
                        <div className="ibox-title bluetop">
                            <h2>Dispute Credit Transaction</h2>
                        </div>
                        <div className="ibox-content">
                            <div className='example' className="row"  >
                                <div className="col-lg-12">
                                    <div className='step-progress' id='main-contents'>

                                        <ProgressBar />
                                        <StepZilla
                                            steps={steps}
                                            nextButtonCls="btn btn-prev btn-primary  pull-right stepZillNextabt"
                                            backButtonCls="btn btn-next btn-primary  pull-left stepZillRightabt"
                                            nextTextOnFinalActionStep="Submit"
                                            prevBtnOnLastStep={false}
                                            stepsNavigation={false}
                                            showNavigation={false}
                                            showSteps={false}
                                            onStepChange={(step) => this.props.setCurrentStep(step)}
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        userProfile: state.transactionDisputeReducer.userProfile,
        transactionDisputes: state.transactionDisputeReducer.transactionDisputes,
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        setCurrentStep: (step) => dispatch(stepActions.setCurrentStep(step)),
        accessToken: (accessToken) => dispatch(actions.accessToken(accessToken)),
        remoteGetUserProfile: (accessToken) => dispatch(actions.remoteGetUserProfile(accessToken)),
        addDispute: (data) => dispatch(actions.addDispute(data)),
        setUserProfile: (payload) => dispatch(actions.setUserProfile(payload)),

    }
};

export default connect(mapStateToProps, mapDispatchToProps)(MainForm);



